	module.exports = {"data":{"markdownRemark":{"html":"<p>In the early hours of the morning (in the name of the father), it is a political film, based on the relationship between the Irish Republican Army and the British authorities, reflecting a profound contradiction there and the impact of such contradictions on politics and people's emotions. The four young people who were in the society were accused by the police of being the mastermind of an explosion. The police used various methods to recruit them and finally achieved their goals. And they also put several of their relatives into prison. After the real Republican Army was captured and accounted for, the police still did not reverse the case, and they continued to stay in prison. Later the father died in prison. A female lawyer, after painstaking efforts, found out the truth and after being imprisoned for fifteen years, they were all released by the court. After such an experience, the young man seems to mature, and finally he said that the truth must be ascertained. However, it seems that there is no clear result at the end.</p>\n<p>The film has a real social background and reflects the irrational political and non-democratic politics that exist in Western society. The emergence of this kind of politics is the result of psychological expansion and social contradictions.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0161.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0161.md","frontmatter":{"title":"October 18th Tuesday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0161.md absPath of file >>> MarkdownRemark"}}

